'use client';

import { useState } from 'react';

export default function Dashboard() {
  const [students] = useState(42);
  const [teachers] = useState(8);
  const [sessions] = useState(15);
  const [stock] = useState(156);

  return (
    <div>
      <h2>Tableau de bord</h2>
      <p style={{ color: '#666', marginBottom: 20 }}>
        Vue d'ensemble de l'école de cuisine Royaume des Saveurs
      </p>

      <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(200px, 1fr))', gap: 20 }}>
        <div style={{ background: '#fff', padding: 16, borderRadius: 8, textAlign: 'center' }}>
          <div style={{ fontSize: 28, fontWeight: 700, color: '#8b4513' }}>{students}</div>
          <div>Étudiants</div>
        </div>

        <div style={{ background: '#fff', padding: 16, borderRadius: 8, textAlign: 'center' }}>
          <div style={{ fontSize: 28, fontWeight: 700, color: '#8b4513' }}>{teachers}</div>
          <div>Professeurs</div>
        </div>

        <div style={{ background: '#fff', padding: 16, borderRadius: 8, textAlign: 'center' }}>
          <div style={{ fontSize: 28, fontWeight: 700, color: '#8b4513' }}>{sessions}</div>
          <div>Séances</div>
        </div>

        <div style={{ background: '#fff', padding: 16, borderRadius: 8, textAlign: 'center' }}>
          <div style={{ fontSize: 28, fontWeight: 700, color: '#8b4513' }}>{stock}</div>
          <div>Articles en stock</div>
        </div>
      </div>
    </div>
  );
}